<template lang="pug">
#PageContainer
    .blockTitle Videos
    .videoContainer
        .videoBlock(v-for="video in videos")
            youtube.video(:video-id="video.src" player-width="400" player-height="225")
            .videoIntro
                .videoTitle {{video.title}}
                .videoProject {{video.type}} | 
                    span {{video.project}}
</template>

<script>
export default {
    components: {
    },
    data () {
        return {
            videos: [
                {
                    title: "Clinical-friendly Drug Psychotherapy with VR and Biofeedback Technologies (CHI 2021 LBW)",
                    src: "mtVRF1HeZgI",
                    project: "Clinical-friendly Drug Psychotherapy with Virtual Reality and Biofeedback Technologies",
                    type: "Paper"
                },
                {
                    title: "TechLifeProbe in the poster session of ACM UbiComp 2021",
                    src: "vGVaI6lkbeU",
                    project: "Clinical-friendly Drug Psychotherapy with Virtual Reality and Biofeedback Technologies",
                    type: "Paper"
                },
                {
                    title: "Enabling Social Sensing with Intimate Partner ",
                    src: "7gU0Zqo1uDs",
                    project: "Enabling Social Sensing to Share Personal Data with Intimate Partner for the Health and Wellbeing of Long-Hour workers",
                    type: "Paper"
                }
            ]
        }
    }
}
</script>


<style lang="sass" scoped>
@import "@/style/value.sass"
#PageContainer
    max-width: 1280px
    width: 80%
    margin: auto
    padding: 20px
.videoContainer
    display: flex
    margin: 0 auto
    flex-wrap: wrap
.video
    margin: 5px
.videoTitle
    font-size: 14px
    font-weight: 500
.videoProject
    font-size: 12px
.videoIntro
    margin: 0 auto
    text-overflow: ellipsis
    width: 400px 
    
    
</style>



