            .staffNameCN 游創文
        .staffIntro {{introText}}
    .memberSectionTitle.sectionContainter About Me
        .sectionContent {{aboutText}}
        .sectionContent {{aboutTextP2}}
    .memberSectionTitle.sectionContainter Course
            .courseBlock(v-for="course in courses")
                ul.sectionContent
                    li.courseYear(v-html="course.time")
                    ul(v-for="item in course.courseList")
                        li.courseName(v-html="item.courseName")
    .memberSectionTitle.sectionContainter Award
        ul.awardList.sectionContent
            li(v-for="award in awards") {{award.time+ "&nbsp&nbsp&nbsp" +award.awardName}}
                span.highlight {{award.conference}}
    .memberSectionTitle.sectionContainter Talk
        ul.awardList.sectionContent
            li(v-for="talk in talks") {{talk.time+ "&nbsp&nbsp" +talk.talk}}
    .memberSectionTitle.sectionContainter Professional Service
            .courseBlock(v-for="service in services")
                ul.sectionContent
                    li.courseYear(v-html="service.category")
                    ul(v-for="item in service.serviceList")
                        li.courseName(v-html="item.service")
    .memberSectionTitle.contactBlock Contact
        .courseBlock
            ul.sectionContent
                li.contactText {{contact.office}}
                li.contactText {{contact.email}}
</template>

