    data () {
        return {
        }
    }
}
</script>


<style lang="sass" scoped>
@import "@/style/value.sass"
#PageContainer
    max-width: 1280px
    width: 80%
    margin: auto
    padding: 20px
.newsDate
    margin-left: auto 
    margin-right: 0
    font-weight: 500
    color: #707070
.newsTitle
    display: flex
    float:left
    font-weight: 500
    color: #3F3F3F
.newsTitleBlock
    overflow:auto
    display: flex
a
    color: $c-sec
    font-weight: 500
</style>



