<template lang="pug">
#PageContainer
    .blockTitle FAQs
    .faqContent.questionNomral
        ul
            li.questionHighlight How to get involved in UbiComp Lab at NTHU?
            p We are looking for prospective Ph.D., Master, and Undergraduate students to join us. For prospective students, Please follow the instructions listed below:
            ul
                li.answerHightlight Ph.D and graduate students
                    p We can only take prospective students who have been admitted to the NTHU 
                        a(href="https://ims.site.nthu.edu.tw") IMS
                        | , 
                        a(href="https://iphd.site.nthu.edu.tw") IPHD
                        | , 
                        a(href="https://isa.site.nthu.edu.tw/") ISA
                        | , 
                        a(href="http://art.nthu.edu.tw") GIAT
                        | , 
                        | For those who have admitted those Ph.D./master programs, please email your CV and transcript to Prof. Chuang-Wen You ! 
                li.answerHightlight Undergraduate students
                    p We welcome all undergraduate students to work with us.  Please email Prof. Chuang-Wen You to make an appointment!
</template>

<script>
export default {
    components: {
    },
    data () {
        return {
        }
    }
}
</script>


<style lang="sass" scoped>
@import "@/style/value.sass"
#PageContainer
    max-width: 1280px
    width: 80%
    margin: auto
    padding: 20px
.questionHighlight
    font-size: 20px
    font-weight: 500
    color: #3F3F3F
.questionNomral
    font-size: 16px
    color: #3F3F3F
    line-height: 0.6
    font-weight: 500
.answerHightlight
    font-size: 16px
    font-weight: 500
    line-height: 0.6
    list-style-type: disc
    color: #000
a
    color: #C7A016
    font-weight: 700
</style>



